.popover_html_cont {
  display: none;
}

.popover-body {
  padding: 12px 20px;
}
.popover-body .dropdown-menu {
  position: relative;
  box-shadow: none;
  float: none;
  min-width: 10rem;
  padding: 0;
  margin: 0;
  border: 0;
}

.user {
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 0.5rem 1.5rem;
  margin-bottom: 0.5rem;
  border-bottom: 1px solid #dedede;
}
.user .h3 {
  color: #e2e2e2;
}
.user p {
  margin-bottom: 0;
}

.avatar {
  margin-bottom: 15px;
}
.avatar img {
  width: 70px;
  height: 70px;
  object-fit: cover;
  object-position: center center;
  border-radius: 50%;
}

/*---------------------------------------------</popup>------------------*/
/*-------------------------------<nav-head>----------------------------------*/
.nav-head {
  background: #2e435e;
  background: linear-gradient(144deg, #2e435e 49%, #1eb6db 100%);
  position: relative;
}
.nav-head .logo-side {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 50px 20px 10px 20px;
  color: #ffffff;
  font-size: 50px;
}
.nav-head .logo-side-img {
  width: 60px;
}
.nav-head .user-text {
  padding: 0px 20px;
  padding-bottom: 10px;
  text-align: center;
}
.nav-head .user-text p {
  color: #fff;
  margin-bottom: 0;
}
.nav-head a {
  color: rgba(255, 255, 255, 0.7);
}

.nav-icon.popover-cont.close_icon {
  position: absolute;
  right: 20px;
  top: 7px;
  font-size: 27px;
  color: rgba(255, 255, 255, 0.8);
}
.nav-icon.popover-cont.close_icon img {
  width: 15px;
}

