body {
  background-color: #e2e2e2;
}

.c-pad {
  padding: 0 20px 20px;
}
.c-pad .h3 {
  margin-top: 10px;
}
.c-pad .h2 {
  margin-top: 15px;
}

.offer {
  border-top-left-radius: 4px;
  border-top-right-radius: 4px;
  overflow: hidden;
}
.offer.clip {
  -webkit-clip-path: circle(81% at 50% -28%);
  clip-path: circle(81% at 50% -28%);
}

p {
  color: #616060;
  font-size: 15px;
}

.full_height {
  height: 100%;
}

.container {
  transition: max-width 0.3s;
}
.main_body .container {
  max-width: 1400px;
  padding-bottom: 50px;
}
.full_container .container {
  max-width: 100%;
}

.main_body {
  margin-top: 46px;
  min-height: calc(100vh - 46px);
  background-color: #F3F3F4;
  padding-top: 15px;
  transition: margin-left 0.3s;
}
.main_body.m2 {
  background-color: #e2e2e2;
  height: auto;
  display: flex;
  flex-direction: column;
}
.no-menu .main_body {
  margin-left: 0;
}
.only_icons .main_body {
  margin-left: 60px;
}
.full_menu .main_body {
  margin-left: 200px;
}

.h-0 {
  height: 0;
}

.text_con {
  box-shadow: 0px 2px 1px -1px rgba(0, 0, 0, 0.2), 0px 1px 1px 0px rgba(0, 0, 0, 0.14), 0px 1px 3px 0px rgba(0, 0, 0, 0.12);
  background-color: #fff;
  border-radius: 4px;
  padding: 20px;
  margin: 15px 0px;
}
.text_con + .text_con {
  margin-top: 30px;
}
.text_con.title {
  box-shadow: none;
  background-color: transparent;
  padding: 0;
}
.text_con.full_height {
  height: calc(100% - 30px);
}
.text_con.main_cat {
  display: flex;
  flex-direction: row;
  align-items: center;
}
.text_con .flex {
  display: flex;
}
.text_con .flex .icon1 {
  width: 30%;
}
.text_con .flex .icon1 img {
  width: 100%;
}
.text_con p, .text_con a:not(.dropdown-item):not(.btn) {
  padding: 0;
  margin: 0;
}
.text_con p.icon, .text_con a:not(.dropdown-item):not(.btn).icon {
  margin-left: 10px;
}
.text_con .listrow {
  border-bottom: 1px solid #dedede;
  padding: 12px 0;
  position: relative;
}
.text_con .listrow img.bg_icon {
  right: 0;
  position: absolute;
  top: 20px;
}
.text_con .listrow:last-of-type {
  padding-bottom: 0;
  border-bottom: 0;
}
.text_con .listrow:first-of-type {
  padding-top: 0;
}
.text_con .listrow:first-of-type img.bg_icon {
  top: 0px;
}
.text_con a.icon {
  margin-left: 10px;
}
.text_con a.icon img {
  width: 30px;
}

p.num {
  font-size: 25px;
  color: white;
}

.popover, .dropdown-menu {
  border: 0;
  box-shadow: 0px 11px 15px -7px rgba(0, 0, 0, 0.2), 0px 24px 38px 3px rgba(0, 0, 0, 0.14), 0px 9px 46px 8px rgba(0, 0, 0, 0.12);
}

.flex-r {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.flex-cc {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
}

.offer img {
  width: 100%;
}

p.description {
  padding: 10px 0;
}

.custom_bg {
  height: 0;
  width: 100%;
  background: #e2e2e2;
  background: linear-gradient(0deg, #e2e2e2 0%, white 100%);
  padding-bottom: 50%;
  position: relative;
}
.custom_bg .inner {
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  top: 0;
  width: 100%;
  height: 100%;
