[type=radio] + label:after, [type=checkbox] + label:after {
  background-color: #fff;
  bottom: 4px;
  content: "";
  height: 26px;
  left: 4px;
  position: absolute;
  transition: 0.4s;
  width: 26px;
  border-radius: 50%;
  box-shadow: -1px 0 3px 1px rgba(0, 0, 0, 0.3);
  z-index: 0;
}
[type=radio]:checked, [type=radio]:not(:checked), [type=checkbox]:checked, [type=checkbox]:not(:checked) {
  position: absolute;
  left: -9999px;
}
[type=radio]:checked + label, [type=radio]:not(:checked) + label, [type=checkbox]:checked + label, [type=checkbox]:not(:checked) + label {
  position: relative;
  cursor: pointer;
  line-height: 22px;
  display: block;
  color: #666;
  height: 34px;
  width: 60px;
}
[type=radio]:checked + label:before, [type=radio]:not(:checked) + label:before, [type=checkbox]:checked + label:before, [type=checkbox]:not(:checked) + label:before {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  border-radius: 100%;
  background-color: #e4e1e1;
  border-radius: 34px;
  transition: background-color 0.3s;
  z-index: 0;
}

[type=radio]:checked + label:before, [type=checkbox]:checked + label:before {
  animation: example 0.3s linear;
  background-color: white;
}
[type=radio]:checked + label:after, [type=checkbox]:checked + label:after {
  left: 30px;
}

@keyframes example {
  0% {
    box-shadow: 0px 0px 4px 0px rgba(255, 255, 255, 0.15);
